
const Buttons = ({onClick, onClear, onEquals}) => {
    return (
        <div id="buttons">
            <button onClick={onClear} id="clear">AC</button>
            <button onClick={onClick} id="divide" className="calc">/</button>
            <button onClick={onClick} id="multiply" className="calc">x</button>
            <button onClick={onClick} id="seven" className="numeric">7</button>
            <button onClick={onClick} id="eight" className="numeric">8</button>
            <button onClick={onClick} id="nine" className="numeric">9</button>
            <button onClick={onClick} id="subtract" className="calc">-</button>
            <button onClick={onClick} id="four" className="numeric">4</button>
            <button onClick={onClick} id="five" className="numeric">5</button>
            <button onClick={onClick} id="six" className="numeric">6</button>
            <button onClick={onClick} id="add" className="calc">+</button>
            <button onClick={onClick} id="one" className="numeric">1</button>
            <button onClick={onClick} id="two" className="numeric">2</button>
            <button onClick={onClick} id="three" className="numeric">3</button>
            <button onClick={onEquals} id="equals">=</button>
            <button onClick={onClick} id="zero">0</button>
            <button onClick={onClick} id="decimal">.</button>

        </div>
    )
}

export default Buttons
