import Display from './components/Display'
import Buttons from './components/Buttons'
import {useState} from 'react'

function App() {
  let [lowerText, setLowerText] = useState("0");
  let [upperText, setUpperText] = useState("");
  let [lastButton, setLastButton] = useState("fff");
  const type = (x) => {
    if(lowerText.length >= 16 && upperText && x.target.className !== "calc"){
      setLowerText("DIGIT LIMIT MET");
      setLastButton("fff");
    }else{
      if(x.target.className === "numeric"){
        if(lastButton.className === "calc"){
          setUpperText(upperText + x.target.innerText);
          setLowerText(x.target.innerText);
        }else if(lastButton === "fff"){
          setUpperText(x.target.innerText);
          setLowerText(x.target.innerText);
        }else if(lowerText.charAt(0) === "0" && lowerText.charAt(1) !== "."){
          setUpperText(x.target.innerText);
          setLowerText(x.target.innerText);
        }else{
          setUpperText(upperText + x.target.innerText);
          setLowerText(lowerText + x.target.innerText);
        }
      }else if(x.target.className === "calc"){
        setLowerText(x.target.innerText);
        if(lastButton === 'fff'){
          setUpperText(lowerText + x.target.innerText);
        }else if(lastButton.className === "calc" && x.target.innerText !== "-"){
          if(lastButton.innerText !== "-"){
            setUpperText(upperText.slice(0, -1) + x.target.innerText );
          }else{
            setUpperText(upperText.slice(0, -2) + x.target.innerText);
          }
        }else{
          setUpperText(upperText + x.target.innerText);
        }
      }else if(x.target.id === "zero"){
        if(lastButton.className === "calc"){
          setUpperText(upperText + "0");
          setLowerText("0");
        }else if(upperText === "" && lowerText === "0" || upperText === "0" && lowerText === "0"){
          setUpperText("0");
        }else if(lastButton === "fff"){
          setUpperText("0");
          setLowerText("0");
        }else{
          setUpperText(upperText + "0");
          setLowerText(lowerText + "0");
        }
      }else if(x.target.id === "decimal"){
        if(lastButton === "fff"){
          setUpperText("0.");
          setLowerText("0.");
        }else if(lastButton.className === "calc"){
          setUpperText(upperText + "0.");
          setLowerText("0.");
        }else if(lowerText.indexOf(".") === -1){
          setUpperText(upperText + ".");
          setLowerText(lowerText + ".");
        }
      }
    }
    setLastButton(x.target);
  }

  const clearDisplay = () => {
    setLowerText("0");
    setUpperText("");
  }

  const calculate = () => {
    let result = 0;
    if(upperText === ""){
      setLowerText("0");
      setUpperText("");
    }else if(upperText.indexOf("=") === -1){
      upperText = upperText.replace("x", "*");
      result = eval(upperText);
      console.log("result " + result);
      result = result.toFixed(15);
      result = result.toString();
      console.log("result " + result);
      while(result.length > 15 && result.indexOf(".") !== -1){
        console.log("result.length = " + result.length)
        if(result.charCodeAt(result.length-1) > 52 && result.charCodeAt(result.length-1) < 58){
          console.log("last number is big");
          result = result.slice(0, -1);
          console.log("shorten to " + result);
          let lastNumber = result.charAt(result.length-1);
          while(lastNumber === "9"){
            console.log("last number = 9");
            result = result.slice(0, -1);
            console.log("deleted");
            lastNumber = result.charAt(result.length-1);
          }
          console.log("now result is" + result);
          let newCode = lastNumber.charCodeAt(0) + 1;
          result = result.slice(0, -1) + String.fromCharCode(newCode);
          console.log("corrected to " + result);
        }else{
          console.log("last number is small");
          result = result.slice(0, -1);
          console.log("reduced to " + result);
        }
      }
      while(result.charAt(result.length-1) === "." || result.charAt(result.length-1) === "0" && result.indexOf(".") !== -1){
        result = result.slice(0, -1);
        console.log("deleted 0 or ., now result is " + result);
      }
      setUpperText(upperText + "=" + result);
      if (result.length > 15){
        setLowerText("DIGIT LIMIT MET");
      }else{
        setLowerText(result);
      }
    }
    setLastButton("fff");
  }

  return (
    <div className="App">
      <div id="calculator">
        <Display lowerText={lowerText} upperText={upperText}/>
        <Buttons onClick={type} onClear={clearDisplay} onEquals={calculate}/>
      </div>
    </div>
  );
}

export default App;
